<template>
    <div
        class="date-time-picker-popup"
        :style="styles"
        tabindex="0"
    >
        <DatePicker
            :year="shownYear"
            :month="shownMonth"
            :selected-year="selectedYear"
            :selected-month="selectedMonth"
            :selected-day="selectedDay"
            @select="selectDay"
            @show="showDate"
        />
    </div>
</template>

<script>
    import DatePicker from '@lapaliv/vue-picker/src/components/DatePicker';
    import ShownMonthMixin from '@lapaliv/vue-picker/src/mixins/ShownMonthMixin';
    import SelectedMonthMixin from '@lapaliv/vue-picker/src/mixins/SelectedMonthMixin';

    export default {
        name: 'DateTimePickerPopup',
        components: {DatePicker},
        mixins: [ShownMonthMixin, SelectedMonthMixin],
        props: {
            shownYear: {
                type: Number,
                required: true,
            },
            selectedYear: {
                type: null,
                required: true,
            },
            selectedDay: {
                type: null,
                required: true,
            },
        },
        data() {
            return {
                offsetLeft: 0,
                offsetTop: 0,
                offsetHeight: 0,
                offsetWidth: 0,
            };
        },
        computed: {
            styles() {
                let result = {};

                if (this.offsetLeft + this.offsetWidth > window.innerWidth + window.scrollX) {
                    result.right = 0;
                }

                //if (this.offsetTop + this.offsetHeight > window.innerHeight) {
                //    result.bottom = 0;
                //}

                return result;
            },
        },
        mounted() {
            this.defineOffsetLeft();
            this.defineOffsetTop();
            this.defineOffsetSize();
            this.listenResizeWindow();
        },
        methods: {
            listenResizeWindow() {
                window.addEventListener('resize', () => {
                    this.defineOffsetLeft();
                    this.defineOffsetTop();
                });
            },
            selectDay(year, month, day) {
                this.$emit('select', year, month, day);
            },
            showDate(year, month) {
                this.$emit('show', year, month);
            },
            defineOffsetLeft() {
                let result = 0;
                let node = this.$el;

                if (node) {
                    result += node.offsetLeft;

                    while (node && node.offsetParent) {
                        node = node.offsetParent;
                        if (node) {
                            result += node.offsetLeft;
                        }
                    }
                }

                this.offsetLeft = result;
            },
            defineOffsetTop() {
                let result = 0;
                let node = this.$el;

                if (node) {
                    result += node.offsetTop;

                    while (node && node.offsetParent) {
                        node = node.offsetParent;
                        if (node) {
                            result += node.offsetTop;
                        }
                    }
                }

                this.offsetTop = result;
            },
            defineOffsetSize() {
                if (this.$el) {
                    this.offsetWidth = this.$el.offsetWidth;
                    this.offsetHeight = this.$el.offsetHeight;
                }
            },
        },
    };
</script>
